.garageDoorContainer {
  --scroll-progress: 0;
  --revealed-y: 0;
  --door-height: 100vh;
  position: relative;
  width: 100%;

  --shadow-radius: var(--border-radius-40);

  @media (--md) {
    --shadow-radius: var(--border-radius-large);
  }
}

.shadow {
  z-index: var(--z-index-1);
}

.door {
  position: relative;
  z-index: var(--z-index-1);
  overflow: hidden;
  background-color: var(--color-grey-00);
  border-radius: var(--bottom-rounded-module-border-radius);
}

.revealed {
  margin-top: 0;

  @media (--sm) {
    margin-top: -100vh;
    transform: translate3d(0, var(--covered-parallax-progress), 0);
  }
}

.revealed::after {
  display: block;
  height: 0;
  background: var(--revealed-module-background, var(--color-grey-00));
  content: '';

  @media (--sm) {
    height: 100vh;
  }
}

.sticky {
  position: sticky;
  top: 0;
  margin-top: calc(var(--border-radius-large) * -2);

  @media (--sm) {
    margin-top: 0;
  }

  &.extraPadding {
    @media (--md) {
      padding-top: min(var(--spacer-240), 40vh);
    }
  }
}
