.footer {
  --footer-progress: 0;
  position: relative;
  z-index: 1;
  padding-top: var(--spacer-48);
  padding-bottom: var(--spacer-48);
  margin-top: calc(var(--border-radius-20) * -1);
  background: var(--color-alpha-black-100);
  border-radius: var(--border-radius-20) var(--border-radius-20) 0 0;
  color: var(--color-grey-60);
  transition: transform 0.1s var(--ease-in-out);

  @media (--sm) {
    padding-top: var(--spacer-96);
    margin-top: calc(var(--border-radius-40) * -1);
    border-radius: var(--border-radius-40) var(--border-radius-40) 0 0;
    transform: translate3d(0, var(--footer-progress), 0);
  }

  .footerButtonContainer {
    --icon-size: 2rem;
  }
}

.rowPrimary {
  composes: grid from '~g';
  padding-bottom: var(--spacer-64);

  @media (--sm) {
    padding-bottom: var(--spacer-160);
  }
}

.rowSecondary {
  padding: var(--spacer-40) 0 0;
  border-top: 1px solid var(--color-grey-30);

  @media (--sm) {
    composes: grid from '~g';
    display: grid;
    flex-flow: row nowrap;
    padding: var(--spacer-48) 0 0;
    border-top: 1px solid var(--color-grey-10);
  }
}

.columnLogo {
  grid-column: start / end;

  @media (--sm) {
    grid-column: start / span 3;
  }

  svg {
    width: 100%;
  }
}

.primaryLogo {
  height: 2.4rem;

  @media (--sm) {
    height: 2.6rem;
  }

  @media (--md) {
    height: 3.2rem;
  }
}

.columnPrimaryLinks {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: 4;
  grid-column: start / end;

  @media (--sm) {
    --subgrid-start: col 4;
    --subgrid-width: 12;
    grid-column: col 4 / span 10;
  }

  @media (--md) {
    --subgrid-start: col 4;
    --subgrid-width: 10;
    grid-column: col 5 / span 10;
  }
}

.columnPrimaryLink {
  padding: 0 0 var(--spacer-32);
  border-bottom: 1px solid var(--color-grey-15);
  grid-column: start / end;

  @media (--sm) {
    padding: 0.7em 0 var(--spacer-24);
    padding-bottom: 0;
    border-bottom: none;
    grid-column: auto / span 3;
  }

  @media (--md) {
    grid-column: auto / span 2;
  }

  &:last-of-type {
    border-bottom: none;
  }
}

.columnBottomLeftCopyright {
  z-index: var(--z-index-0);
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--spacer-12);
  grid-column: start / end;

  @media (--sm) {
    z-index: initial;
    grid-column: start / span 11;
  }

  @media (--md) {
    flex-wrap: nowrap;
    grid-column: start / span 12;
  }
}

.columnBottomLeftLinks {
  z-index: var(--z-index-0);
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--spacer-12);

  @media (--md) {
    width: auto;
  }
}

.columnBottomRightSocials {
  display: flex;
  flex-flow: row nowrap;
  margin: var(--spacer-24) 0 0;
  gap: var(--spacer-24);
  grid-column: start / end;

  @media (--sm) {
    margin-top: 0;
    gap: var(--spacer-20);
    grid-column: col 12 / end;
    justify-self: end;
  }

  @media (--md) {
    gap: var(--spacer-24);
    grid-column: col 13 / end;
  }
}

.socialIcon {
  width: 2rem;
  height: 2rem;

  * {
    fill: var(--color-grey-60);
  }
}

.columnBottomLogo {
  composes: meta from '~typo';
  z-index: var(--z-index-1);
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  grid-column: start / end;

  @media (--sm) {
    grid-column: start / span 3;
  }

  @media (--md) {
    grid-column: start / span 2;
  }

  svg {
    width: 7rem;

    * {
      fill: var(--color-grey-60);
    }
  }
}

.bottomLogoSubtext {
  display: block;
}

.bottomLogoLink {
  color: var(--color-grey-60);
  text-decoration: none;
}

.bottomLeftItem {
  composes: meta from '~typo';
}

.bottomRightLink {
  color: var(--color-grey-60);
  text-decoration: none;
}
