.footerGroup {
  position: relative;
}

.groupLink {
  composes: button from '~typo';
}

.linkItem {
  composes: buttonSecondary from '~typo';
  position: relative;
  display: block;
  margin-bottom: var(--spacer-12);
  color: var(--color-grey-60);
  line-height: 1.5;
  text-decoration: none;

  @media (--sm) {
    margin-bottom: var(--spacer-12);
  }

  &:hover {
    color: var(--color-grey-80);
  }
}

.desktopFooterGroup {
  display: none;

  .groupLink {
    position: relative;
    display: block;
    margin-bottom: var(--spacer-20);
    color: var(--color-grey-100);
    text-decoration: none;
  }

  @media (--sm) {
    display: block;
  }
}

.mobileFooterGroup {
  @media (--sm) {
    display: none;
  }

  .groupLink {
    padding: var(--spacer-32) 0;
    margin-bottom: -3rem;
  }
}

.groupContent {
  & > * {
    &:first-child {
      padding-top: var(--spacer-24);
    }
  }
}
