.door {
  position: relative;
  z-index: var(--z-index-1);
  overflow: hidden;
  background-color: var(--color-grey-00);
  border-radius: var(--top-rounded-module-border-radius);
}

.covered {
  --covered-opacity-progress: 1;

  @media (--sm) {
    margin-bottom: -100vh;
    opacity: var(--covered-opacity-progress);
    transform: translate3d(0, var(--covered-parallax-progress), 0);
  }
}

.covered::before {
  display: block;
  content: '';

  @media (--sm) {
    height: 100vh;
  }
}

.sticky {
  position: sticky;
  bottom: 0;
}

.shadow {
  z-index: var(--z-index-1);
}
