.footerImage {
  position: absolute;
  z-index: -2;
  width: 100%;
  transform: translate(0, -100%);

  --image-aspect-ratio: 0.8;

  @media (--xs) {
    --image-aspect-ratio: 1.2;
  }
  @media (--md) {
    --image-aspect-ratio: 2.2;
  }
  @media (--lg) {
    --image-aspect-ratio: 2.2;
  }
  @media (--xl) {
    --image-aspect-ratio: 2.2;
  }
}
